  color: #2d3159;
  border-color: #2d3159;
  background: transparent;
}

.c-btn.is--outline:hover,
.c-btn.is--outline:focus,
.c-btn.is--outline:active {
  color: #fff;
  background: #2d3159;
  border-color: transparent;
}


